.popup {
    position: fixed;
    background-color: rgba(0, 0, 0, 0.5);
    width: 100%;
    height: 100%;
    top: 0;
    right: 0;
    display: flex;
    z-index: 2;
    align-items: center;
   justify-content: center;
   opacity: 0;
   visibility: hidden;
   transition: all .5s ease;
}

